import { ChangeDetectorRef, Component, Input, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { LoadingController, ModalController } from '@ionic/angular';
import { FirebaseCollections } from 'src/app/constants/fb-collections';
import { CommonService } from 'src/app/services/common/common.service';
import { DataService } from 'src/app/services/data/data.service';
import { StateService } from 'src/app/services/state/state.service';
import { StorageService } from 'src/app/services/storage/storage.service';

@Component({
  selector: 'app-answer-sheets',
  templateUrl: './answer-sheets.page.html',
  styleUrls: ['./answer-sheets.page.scss'],
})
export class AnswerSheetsPage implements OnInit {

  @Input() public data;
  public remarksForm: FormGroup;
  public remarks: string = '';
  private remarkId: string;
  private course: any;
  private testId: string;
  private valuedAnswerSheets: any = {};

  constructor(
    private cd: ChangeDetectorRef,
    private dataService: DataService,
    private stateService: StateService,
    private storageService: StorageService,
    private loadingCtrl: LoadingController,
    private commonService: CommonService,
    private modalCtrl: ModalController,
    private formBuilder: FormBuilder
  ) {
    this.course = this.stateService.getData('course');
    this.testId = this.stateService.getData('test')?.id;
    this.dataService.getQuestionPaperByTestId(this.testId).subscribe((resp: any) => {
      this.valuedAnswerSheets = resp[0];
      this.cd.detectChanges();
    });
  }

  ngOnInit() {
    this.dataService.getRemarksByTestId(this.testId, this.data.student_id).subscribe((response: any) => {
      this.remarksForm.patchValue({
        ...response[0]
      });
      this.remarks = response[0].remarks;
      this.remarkId = response[0].id;
      this.cd.detectChanges();
    });
    this.createForm();
    this.remarksForm.patchValue({
      course_id: this.course.id,
      course_name: this.course.title,
      test_id: this.data.test.id,
      test_name: this.data.test.title,
      student_id: this.data.student_id,
      student_name: this.data.student_name,
      reviewed_by_id: localStorage.getItem('uid'),
      reviewed_by_email: localStorage.getItem('email'),
    });
  }

  createForm() {
    this.remarksForm = this.formBuilder.group({
      course_id: new FormControl(null, Validators.compose([Validators.required])),
      course_name: new FormControl(null, Validators.compose([Validators.required])),
      test_id: new FormControl(null, Validators.compose([Validators.required])),
      test_name: new FormControl(null, Validators.compose([Validators.required])),
      student_id: new FormControl(null, Validators.compose([Validators.required])),
      student_name: new FormControl(null, Validators.compose([Validators.required])),
      reviewed_by_id: new FormControl(null, Validators.compose([Validators.required])),
      reviewed_by_email: new FormControl(null, Validators.compose([Validators.required])),
      total_marks: new FormControl(null, Validators.compose([Validators.required])),
      description_part: new FormControl(null, Validators.compose([Validators.required])),
      objectivce_part: new FormControl(null, Validators.compose([Validators.required])),
      weightage_marks: new FormControl(null, Validators.compose([Validators.required])),
      predicted_marks: new FormControl(null, Validators.compose([Validators.required])),
      remarks: new FormControl(null, Validators.compose([Validators.required])),
    });
  }

  submitRemarks() {
    this.remarksForm.patchValue({
      remarks: this.remarks
    });
    const body = { ...this.remarksForm.value };
    if (this.commonService.isValidData(this.remarkId)) {
      body.id = this.remarkId;
    }
    this.dataService.update(FirebaseCollections.STUDENT_ACTIVITY, body).then(resp => {
      this.commonService.presentToast('Remarks submitted');
      this.reset();
      this.dataService.getRemarksByTestId(this.testId, this.data.student_id).subscribe((response: any) => {
        this.remarksForm.patchValue({
          ...response[0]
        });
        this.remarks = response[0].remarks;
      });
    }, err => {
      console.error('Error while submitting remarks::::::::::::::\n', err);
      if (err.code === 'not-found' || err.code === 'invalid-argument') {
        this.dataService.add(FirebaseCollections.STUDENT_ACTIVITY, this.remarksForm.value).then((resp: any) => {
          // console.log(resp);
          this.commonService.presentToast('Remarks submitted');
          this.reset();
          this.dataService.getRemarksByTestId(this.testId, this.data.student_id).subscribe((response: any) => {
            this.remarksForm.patchValue({
              ...response[0]
            });
            this.remarks = response[0].remarks;
          });
          this.reset();
        }, (error: any) => {
          console.error('Error while adding valued answer sheet:::::::::::\n', error);
        });
      }
    });
  }

  async uploadValuedAnswerSheet(event: any) {
    const selectedFile: any = event.target.files[0];
    const loading = await this.loadingCtrl.create({ message: 'Uploading valued answer sheet...' });
    loading.present();
    try {
      const file = {
        file: selectedFile,
        name: this.course.id + '_G_' + this.testId + '_G_' + selectedFile.name,
        test_id: this.testId
      };
      const upload: any = await this.storageService.pushFileToStorage(file, `uploads/valued_answer_sheets/${file.name}`);
      if (upload && upload.file_url) {
        const valuedAnswerSheets: any = {
          file_url: upload.file_url,
          type: this.commonService.getExtension(selectedFile.name),
          name: selectedFile.name,
          test_id: this.testId,
          student_id: this.data.student_id,
          student_name: this.data.student_name
        };
        if (this.commonService.isValidData(this.valuedAnswerSheets?.id)) {
          valuedAnswerSheets.id = this.valuedAnswerSheets.id;
        }
        this.dataService.update(FirebaseCollections.VALUED_ANSWER_SHEET, valuedAnswerSheets).then((resp: any) => {
          // console.log('resp::::::::::::\n', resp);
          loading.dismiss();
        }, err => {
          loading.dismiss();
          console.error('Error while uploading valued answer sheet:::::::::::\n', { ...err });
          if (err.code === 'not-found' || err.code === 'invalid-argument') {
            this.dataService.add(FirebaseCollections.VALUED_ANSWER_SHEET, valuedAnswerSheets).then((resp: any) => {
              // console.log(resp);
              this.dataService.getScheduleByTestId(this.testId).subscribe((response: any) => {
                this.valuedAnswerSheets = response[0];
              });
            }, (error: any) => {
              console.error('Error while adding valued answer sheet:::::::::::\n', error);
            });
          }
        });
      } else {
        console.error('Error while uploading valued answer sheet:::::::::::\n', upload);
        loading.dismiss();
      }
    } catch (exception: any) {
      console.error('Exception while uploading valued answer sheet:::::::::::\n', exception, { ...exception });
      loading.dismiss();
    }
  }

  close() {
    this.modalCtrl.dismiss();
  }

  reset() {
    this.remarks = '';
    this.remarksForm.reset();
    this.remarksForm.patchValue({
      course_id: this.course.id,
      course_name: this.course.title,
      test_id: this.data.test.id,
      test_name: this.data.test.title,
      student_id: this.data.student_id,
      student_name: this.data.student_name,
      reviewed_by_id: localStorage.getItem('uid'),
      reviewed_by_email: localStorage.getItem('email'),
    });
  }
}
